/* src/components/CourseDetail.css */

.course-detail-container {
    padding: 20px;
    max-width: 900px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
  }
  
  .course-detail-container h1 {
    margin-bottom: 15px;
    font-size: 2rem;
    color: #333;
  }
  
  .course-detail-container p {
    margin: 10px 0;
    font-size: 1rem;
    color: #555;
  }
  
  .course-detail-container ul {
    list-style-type: none;
    padding: 0;
  }
  
  .course-detail-container li {
    margin-bottom: 10px;
    font-size: 0.9rem;
    color: #666;
  }
  
  .course-detail-container button {
    display: inline-block;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    margin-top: 20px;
    margin-right: 10px;
  }
  
  .course-detail-container button:hover {
    background-color: #0056b3;
    transform: scale(1.05);
  }
  
  .course-detail-container button:focus {
    outline: none;
  }
  
  .add-course-button {
    background-color: #28a745;
  }
  
  .add-course-button:hover {
    background-color: #218838;
  }
  
  @media (max-width: 768px) {
    .course-detail-container {
      padding: 15px;
    }
  
    .course-detail-container h1 {
      font-size: 1.5rem;
    }
  
    .course-detail-container p {
      font-size: 0.9rem;
    }
  
    .course-detail-container button {
      font-size: 0.9rem;
      padding: 8px 16px;
    }
  }
  