/* src/components/StudentDashboard.css */
.student-dashboard-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    background: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .student-dashboard-container h1 {
    font-size: 2rem;
    color: #333;
  }
  
  .student-dashboard-container ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .course-item {
    
    margin-bottom: 20px;
    padding: 10px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .course-thumbnail {
    width: 150px;
    height: auto;
    border-radius: 8px;
    margin-right: 20px;
  }
  
  .course-details {
    flex: 1;
  }
  
  .course-details h3 {
    margin: 0 0 10px;
  }
  
  .course-details p {
    margin: 5px 0;
  }
  