/* src/components/CourseList.css */

.course-list-container {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .course-list-container h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .course-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  .course-item {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    max-width: 300px;
    width: 100%;
    background-color: #fff;
    text-align: center;
  }
  
  .course-item img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .course-item h3 {
    margin: 15px 0;
    font-size: 1.2rem;
  }
  
  .course-item p {
    margin: 0;
    padding: 0 15px;
    font-size: 0.9rem;
    color: #555;
  }
  
  .course-item:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .button-container {
    text-align: center;
    margin-top: 20px;
  }
  
  .button-container button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button-container button:hover {
    background-color: #0056b3;
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .course-item {
      max-width: 100%;
    }
  
    .button-container button {
      width: 100%;
    }
  }
  